                drag_option:{
                  left: 60,
                  top: 60,
                  w: 300,
                  h: 200,
                  background:'url(' + reader.result + ')0% 0% / 100% 100% no-repeat',
                  rotate: 0,
                  opacity: 1,
                },

                showOff: false
              });

            }
          }
        }
        return false;
